import { Directive, Input, TemplateRef, ViewContainerRef, OnInit } from '@angular/core';
import {ScreenService} from '../services/screen.service';

@Directive({
  selector: '[screenSmall]'
})
export class ScreenSmallDirective implements OnInit {
  private hasView = false;
  constructor(private template: TemplateRef<Object>,
              private screenService: ScreenService,
              private viewContainer: ViewContainerRef) {
    screenService.resize$.subscribe(() => {
      this.onResize();
    });
  }
  ngOnInit() {
    this.onResize();
  }
  onResize() {
    this.screenLargeMeddium = false;
  }
  @Input()
  set screenLargeMeddium(maxCondition) {
    maxCondition = this.screenService.screenWidth < this.screenService.largePixels[1];
    if (maxCondition && !this.hasView) {
      this.hasView = true;
      this.viewContainer.createEmbeddedView(this.template);
    } else if (!maxCondition && this.hasView) {
      this.hasView = false;
      this.viewContainer.clear();
    }
  }
}
