import {Directive, TemplateRef, ViewContainerRef, OnInit, Input} from '@angular/core';
import {ScreenService} from '../services/screen.service';

@Directive({
  selector: '[screenLarge]'
})
export class ScreenLargeDirective implements OnInit {
private hasView = false;
  constructor(private template: TemplateRef<Object>,
              private screenService: ScreenService,
              private viewContainer: ViewContainerRef) {
    screenService.resize$.subscribe(() => {
      this.onResize();
    });
}
onResize() {
    this.screenLarge = false;
  }
  ngOnInit() {
    this.onResize();
  }
@Input()
  set screenLarge(condition) {
    condition = this.screenService.screenWidth >= this.screenService.largePixels[1];
    if (condition && !this.hasView) {
      this.hasView = true;
      this.viewContainer.createEmbeddedView(this.template);
    } else if (!condition && this.hasView) {
      this.hasView = false;
      this.viewContainer.clear();
    }
}
}
