import {Directive, Input, OnInit, TemplateRef, ViewContainerRef} from '@angular/core';
import {ScreenService} from '../services/screen.service';

@Directive({
  selector: '[ScreenTablet]'
})
export class ScreenTabletDirective implements OnInit{
  private hasView = false;
  constructor(private template: TemplateRef<Object>,
              private screenService: ScreenService,
              private viewContainer: ViewContainerRef) {
    screenService.resize$.subscribe(() => {
      this.onResize();
    });
  }
  ngOnInit() {
    this.onResize();
  }
  onResize() {
    this.screenLargeMinimum = false;
  }
  @Input()
  set screenLargeMinimum(minCondition) {
    minCondition = this.screenService.screenWidth < this.screenService.largePixels[0];
    if ( minCondition && !this.hasView) {
      this.hasView = true;
      this.viewContainer.createEmbeddedView(this.template);
      console.log('600');
    } else if ( !minCondition && this.hasView) {
      this.hasView = false;
      this.viewContainer.clear();
    }
  }
}
